<template>
    <div>
        <v-text-field
            v-model="value"
            class="mt-0 pt-0 rangeTextField"
            hide-details
            single-line
            type="number"
            :step="range.step"
            @focus="showSlider = true"
            @blur="showSlider = false"
            :disabled="disabled"
        />
        <template v-if="showSlider">
            <v-slider
                :min="range.min"
                :max="range.max"
                :step="range.step"
                v-model="value"
                class="recordSlider"
                :disabled="disabled"
            />
        </template>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    props: {
        name: {},
        id: {},
        disabled: {
            default: false,
        },
    },

    data() {
        return {
            showSlider: false,
        }
    },

    methods: {
        ...mapActions('urgentCare', [
            'setValueRange',
        ]),
    },

    computed: {
        ...mapGetters('urgentCare', [
            'getValue',
            'getItem',
        ]),

        value: {
            get() {
                let storeValue = this.getValue({
                    'name': this.name,
                    'id': this.id,
                })
                return storeValue
            },

            set(newValue) {
               this.setValueRange({
                   'name' : this.name,
                   'id': this.id,
                   'value': newValue,
                })
            }
        },

        range() {
            let item = this.getItem(this.name)
            return item.range
        },
    },

}
</script>

<style scoped lang="scss">
.recordSlider {
    background-color: white;
    border-radius: 20px;
    border: 1px solid black;
    padding: 10px 20px 0px 20px;
    z-index: 100;
    position: absolute;
    width: 200pt;
    box-shadow: 0px 0px 10px #010101;
    transition: 1s ease-out;
    transform: translate(-200px, 10px);
}

.rangeTextField input[type="number"] {
    border:1px double red;
    text-align: center;
}
</style>
