<template>
    <div class="card-table-row">
        <template v-if="item.type === 'group'">
            <div class="card-table-row" v-for="(groupItem, groupItemIndex, groupItemI) in item.headers" :key="groupItemIndex">
                <div
                    v-if="groupItemI === 0"
                    class="card-table-row-header"
                    :style="'grid-row-end: span ' + Object.keys(item.headers).length + ';  align-self: stretch; padding-top: 10px;'"
                >
                    <div style="writing-mode: vertical-lr; text-orientation: mixed;">
                        {{ item.header }}
                    </div>
                </div>
                <div class="card-table-row-header">
                    {{ groupItem }}
                </div>
                <div v-for="(value, index) in item.values" :key="index">
                    <Record-element
                        :name="name"
                        :id="index"
                        :index="groupItemIndex"
                        :disabled="item.disabled && item.disabled[index]"
                    />
                </div>
            </div>
        </template>
        <template v-else-if="item.type === 'checkbox'">
            <div class="card-table-row">
                <div style="grid-column-end: span 2;" class="card-table-row-header">
                    {{ item.header }}
                </div>
                <template v-for="(item, index) in item.values">
                    <div :key="index">
                        <Record-element :name="name" :id="index" :index="1" :disabled="item.disabled" />
                    </div>
                </template>
            </div>
        </template>
        <template v-else-if="item.type === 'range'">
            <div class="card-table-row">
                <div style="grid-column-end: span 2;" class="card-table-row-header">
                    {{ item.header }}
                </div>
                <template v-for="(value, index) in item.values">
                    <div :key="index" style="margin-bottom: 20px;">
                        <Record-range
                            :name="name"
                            :id="index"
                            :disabled="item.disabled && item.disabled[index]"
                        />
                    </div>
                </template>
            </div>
        </template>
        <template v-else-if="item.type === 'delimiter'">
            <div class="card-table-row">
                <div class="card-table-delimiter" :style="item.style">{{ item.header }}</div>
            </div>
        </template>
        <template v-else-if="item.type === 'checkboxWithInput'">
            <div class="card-table-row">
                <div style="grid-column-end: span 2; width: 95%;" class="card-table-row-header">
                    <v-text-field
                        v-model="item.inputValue"
                        :label="item.header"
                        :suffix="item.suffix"
                        type="number"
                        :rules="item.rules"
                    />
                </div>
                <template v-for="(item, index) in item.values">
                    <div :key="index">
                        <Record-element :name="name" :id="index" :index="1" />
                    </div>
                </template>
            </div>
        </template>
        <template v-else-if="item.type === 'checkboxWithSelector'">
            <div class="card-table-row">
                <div style="grid-column-end: span 2; width: 95%;" class="card-table-row-header" >
                    <v-select
                        v-model="item.select.value"
                        :items="item.select.options"
                        item-text="text"
                        item-value="value"
                        :label="item.header"
                    />
                </div>
                <template v-for="(item, index) in item.values">
                    <div :key="index">
                        <Record-element :name="name" :id="index" :index="1" />
                    </div>
                </template>
            </div>
        </template>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import RecordElement from './RecordElement'
import RecordRange from './RecordRange'

export default {

    components: {
        RecordElement,
        RecordRange,
    },

    props: [
        'name'
    ],

    computed: {
        ...mapGetters('urgentCare', [
            'getItem',
        ]),

        item() {
            return this.getItem(this.name)
        },

        values() {
            return this.item.values
        },
    },

}
</script>
