<template>
    <div class="card-table">
        <Timeline />
        <Record v-for="(record, index) in items" :key="record.index" :name="index" />
    </div>
</template>

<script>
import Vue from 'vue'
import { mapGetters } from 'vuex'
import Timeline from './Timeline'
import Record from './Record'

export default {

    components: {
        Record,
        Timeline,
    },

    computed: {
        ...mapGetters('urgentCare', {
            items: 'getItems',
        }),

        cpapValues() {
            return this.items.cpap.values
        },

        ivlValues() {
            return this.items.ivl.values
        },

        trachealIntubation() {
            return this.items.trachealIntubation.values
        },
    },

    methods: {
        test() {
            console.log(this.items)
        },

        testValues() {
            let values = []

            values = this.items.cpap.values;
            [...values].forEach((value, key) => {
                Vue.set(this.items.ivl.disabled, key, value !== '0')
                Vue.set(this.items.pip.disabled, key, value !== '0')
                if (value !== '0') {
                    Vue.set(this.items.pip.values, key, '')
                    Vue.set(this.items.rate.values, key, '')
                    Vue.set(this.items.tin.values, key, '')
                }
                Vue.set(this.items.rate.disabled, key, value !== '0')
                Vue.set(this.items.tin.disabled, key, value !== '0')
            })

            values = this.items.ivl.values;
            let trachealValues = this.items.trachealIntubation.values.split("");
            let firstValue = true;
            [...values].forEach((value, key) => {
                Vue.set(this.items.cpap.disabled, key, value !== '0')
                // Установка значений блока "Интубация трахеи" согласно "ИВЛ: интубационная трубка"
                if (value === "3" && firstValue) {
                    trachealValues[key] = "1"
                    firstValue = false
                } else {
                    trachealValues[key] = "0"
                }
            })
            Vue.set(this.items.trachealIntubation, "values", trachealValues.join(""))

        },

        testTrachealIntubation() {
            let trachealValues = this.items.trachealIntubation.values.split("")
            let cpapValues = this.items.cpap.values.split("")
            let ivlValues = this.items.ivl.values.split("")
            trachealValues.forEach((v, k) => {
                if (v === "1") {
                    ivlValues[k] = "3"
                    cpapValues[k] = "0"
                } else if (ivlValues[k] === "3" && trachealValues.indexOf("1") === -1) {
                    ivlValues[k] = 0
                }
            })
            Vue.nextTick(() => {
                Vue.set(this.items.cpap, "values", cpapValues.join(""))
                Vue.set(this.items.ivl, "values", ivlValues.join(""))
            })
        },
    },

    watch: {
        cpapValues() {
            this.testValues()
        },

        ivlValues() {
            this.testValues()
        },

        trachealIntubation() {
            this.testTrachealIntubation()
        },
    },

    created() {
        this.testValues()
    },

}

</script>
