<template>
    <input
        type="checkbox"
        @click.prevent
        @mousedown.prevent="changeState($event)"
        @mouseover.prevent="changeState($event)"
        v-model="value"
        :disabled="disabled"
    />
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    props: {
        name: {},
        id: {},
        index: {},
        disabled: {
            default: false,
        },
    },

    methods: {
        ...mapActions('urgentCare', [
            'setValue',
        ]),

        changeState(ev) {
            const MOUSE_LEFT_BUTTON = 1
            if (ev.buttons !== MOUSE_LEFT_BUTTON) {
                return
            }

            this.setValue({
                'name' : this.name,
                'id': this.id,
                'value': this.value ? '0' : this.index,
            })
        },
    },

    computed: {
        ...mapGetters('urgentCare', [
            'getValue',
        ]),

        value: {
            get() {
                let storeValue = this.getValue({
                    'name': this.name,
                    'id': this.id,
                })
                return storeValue == this.index
            }
        },
    },

}
</script>

<style scoped>
input[type="checkbox"] {
    width: 30px;
    height: 30px;
}
</style>
