<template>
    <div xl="12" class="apgar-table">
        <div class="apgar-table-header">Шкала Апгар</div>
        <div class="apgar-table-row">
            <div class="apgar-table-row-header">Время</div>
            <div class="apgar-table-row-header" style="grid-column-end: span 5;">Состояние</div>
            <div class="apgar-table-row-header">Итог</div>
        </div>
        <div v-for="(line, lineIndex) in visibleLines" :key="line.index" class="apgar-table-row">
            <div class="apgar-table-row-header">{{ lineIndex }} мин</div>

                <div class="text-center">
                    Сердцебиение
                    <span class="apgar-table-value-span"
                          :style="{ color: values[lineIndex][apgar_elements.heartRate].value < 1 ? 'red' : '' }"
                    >
                        {{ values[lineIndex][apgar_elements.heartRate].text }}
                    </span>
                </div>

                <div class="text-center">
                    Дыхание
                    <span class="apgar-table-value-span"
                          :style="{ color: values[lineIndex][apgar_elements.respiration].value < 1 ? 'red' : '' }"
                    >
                        {{ values[lineIndex][apgar_elements.respiration].text }}
                    </span>
                </div>

                <div class="text-center">
                    Цвет кожи:
                    <span class="apgar-table-value-span"
                          :style="{ color: values[lineIndex][apgar_elements.appearance].value < 1 ? 'red' : '' }"
                    >
                        {{ values[lineIndex][apgar_elements.appearance].text }}
                    </span>
                </div>

                <div>
                    <v-select
                        :label="apgarItems.muscleTone.caption"
                        :rules="[ v => v !== null || 'Поле на заполнено' ]"
                        :items="apgarItems.muscleTone.possibleValues"
                        item-text="text"
                        item-value="value"
                        v-model="values[lineIndex][apgar_elements.muscleTone].value"
                    />
                </div>

                <div>
                    <v-select
                        :label="apgarItems.reflexes.caption"
                        :rules="[ v => v !== null || 'Поле на заполнено' ]"
                        :items="apgarItems.reflexes.possibleValues"
                        item-text="text"
                        item-value="value"
                        v-model="values[lineIndex][apgar_elements.reflexes].value"
                    />
                </div>
            <div class="apgar-table-result">{{ getApgarLineSum(lineIndex) }}</div>
        </div>

    </div>
</template>

<script>
import Vue from 'vue'
import { mapGetters } from 'vuex'

// Соотношение временной шкалы к строкам Апгар
// Минута: { Индекс столбца временной шкалы : индекс строки Апгар }
const TIMELINE = {
    1: { 'column': 1, 'apgarLine': 0 },
    5: { 'column': 6, 'apgarLine': 1 },
    10: { 'column': 11, 'apgarLine': 2 },
    15: { 'column': 12, 'apgarLine': 3 },
    20: { 'column': 13, 'apgarLine': 4 },
    30: { 'column': 14, 'apgarLine': 5 },
    40: { 'column': 15, 'apgarLine': 6 },
    50: { 'column': 16, 'apgarLine': 7 },
}

const APGAR_ELEMENTS = {
    'heartRate': 0,
    'respiration': 1,
    'appearance': 2,
    'muscleTone': 3,
    'reflexes': 4,
}

export default {

    computed: {
        ...mapGetters('urgentCare', {
            apgarItems: 'getApgarItems',
            values: 'getApgarValues',
            getApgarLineSum: 'getApgarLineSum',
            items: 'getItems',
        }),

        respiration() {
            return this.items.respiration.values
        },

        heartRate() {
            return this.items.heartRate.values
        },

        appearance() {
            return this.items.appearance.values
        },

        cpap() {
            return this.items.cpap.values
        },

        ivl() {
            return this.items.ivl.values
        },

        visibleLines() {
            const filteredLines = {}
            for (let index in this.values) {
                filteredLines[index] = this.values[index]
                if (parseInt(index) === 1) { continue }
                if (!this.isAllFieldsFilled(index)) { break }
                if (this.getApgarLineSum(index) > 5) { break }
            }
            return filteredLines
        },

        timeline() {
            return TIMELINE
        },

        apgar_elements() {
            return APGAR_ELEMENTS
        },

    },

    methods: {

        test() {
            console.log(this.values)
        },

        isAllFieldsFilled(index) {
            let result = true
            this.values[index].forEach(v => {
                let value = parseInt(v.value)
                if (value === null || value === 0) {
                    result = false
                    return false
                }
            })
            return result
        },

        setHeartRate() {
            for(let line in this.timeline) {
                const column = this.timeline[line].column
                const value = parseInt(this.heartRate[column])
                const elementIndex = this.apgar_elements.heartRate
                let newText = "не выбрано"
                switch (value) {
                    case 1: newText = "отсутствует / <60"; break
                    case 2: newText = "<100"; break
                    case 3: newText = ">100"; break
                }
                let newValue = { "text": newText, "value": value }
                Vue.set(this.$store.state.urgentCare.apgar.values[line], elementIndex, newValue)
            }
        },

        setRespiration() {
            for(let line in this.timeline) {
                const column = this.timeline[line].column
                const value = parseInt(this.respiration[column])
                const elementIndex = this.apgar_elements.respiration

                let newText = "не выбрано"
                let newValue = 0
                switch (value) {
                    case 1:
                    case 2: newValue = 1; newText = "отсутствует"; break
                    case 3: newValue = 2; newText = "брадипное, нерегулярное"; break
                    case 4:
                    case 5: newValue = 3; newText = "регулярное, громкий крик и плач"; break
                }

                let suffix = ""
                if (value > 0) {
                    if (parseInt(this.ivl.split("")[column]) !== 0) { suffix = " на фоне ИВЛ" }
                    if (parseInt(this.cpap.split("")[column]) !== 0) { suffix = " на фоне CPAP" }
                }

                let resultValue = { "text": newText + suffix, "value": newValue }
                Vue.set(this.$store.state.urgentCare.apgar.values[line], elementIndex, resultValue)
            }
        },

        setAppearance() {
            for(let line in this.timeline) {
                const column = this.timeline[line].column
                const value = parseInt(this.appearance[column])
                const elementIndex = this.apgar_elements.appearance
                let newText = "не выбран"
                let newValue = 0
                switch (value) {
                    case 1:
                    case 2: newValue = 1; newText = "генерализованная бледность или генерализованный цианоз"; break
                    case 3: newValue = 2; newText = "розовая окраска тела и синюшная окраска конечностей (акроцианоз)"; break
                    case 4: newValue = 3; newText = "розовая окраска всего тела и конечностей"; break
                }
                let resultValue = { "text": newText, "value": newValue }
                Vue.set(this.$store.state.urgentCare.apgar.values[line], elementIndex, resultValue)
            }
        },

    },

    watch: {
        heartRate() {
            this.setHeartRate()
        },

        respiration() {
            this.setRespiration()
        },

        appearance() {
            this.setAppearance()
        },

        cpap() {
            this.setRespiration()
        },

        ivl() {
            this.setRespiration()
        },

    },

    beforeCreate() {
        const default_values = this.$store.state.urgentCare.apgar.default_values
        let minutes = default_values.minutes
        minutes.forEach((value) => {
            Vue.set(this.$store.state.urgentCare.apgar.values, value, [
                Object.assign({}, default_values.heartRate),
                Object.assign({}, default_values.respiration),
                Object.assign({}, default_values.appearance),
                Object.assign({}, default_values.muscleTone),
                Object.assign({}, default_values.reflexes)
            ])
        })
    },

    created() {
        this.setHeartRate()
        this.setRespiration()
        this.setAppearance()
    },

}

</script>

<style scoped>
.apgar-table {
    display: grid;
    max-width: 800px;
    grid-template-columns: 80px repeat(5, 240px) 80px;
    grid-gap: 5px;
}

.apgar-table-header {
    grid-column: 1 / 8;
    font-weight: bold;
    text-align: center;
}

.apgar-table-headers {
    text-align: center;
}

.apgar-table-row {
    display: contents;
}

.apgar-table-row-header {
    font-size: 14pt;
    align-self: center;
    justify-self: center;
}

.apgar-table div {
    align-self: center;
    padding: 10px;
}

.apgar-table-result {
    font-size: 24pt;
    justify-self: center;
}

.apgar-table-value-span {
    font-weight: bolder;
    text-decoration: underline;
}

</style>
