<template>
    <v-row style="max-width: 1400px;">

        <v-col>
            <v-text-field label="Ф.И.О. родильницы" prepend-icon="mdi-account" v-model="mother.fio" />
        </v-col>

        <v-col>
            <v-menu
                ref="menuDate"
                v-model="menuDate"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
                >

                <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                        v-model="formattedDate"
                        label="Дата родов"
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        v-on="on"
                        ></v-text-field>
                </template>

                <v-date-picker
                    v-model="mother.deliveryDate"
                    no-title
                    scrollable
                    @change="menuDate = false"
                />

            </v-menu>
        </v-col>

        <v-col >
            <v-menu
                ref="menuTime"
                v-model="menuTime"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
                >
                <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                        v-model="mother.deliveryTime"
                        label="Время родов"
                        prepend-icon="mdi-clock"
                        v-bind="attrs"
                        v-on="on"
                        ></v-text-field>
                </template>
                <v-time-picker
                    v-if="menuTime"
                    v-model="mother.deliveryTime"
                    format="24hr"
                    full-width
                    @click:minute="menuTime = false"
                >
                    <v-spacer></v-spacer>
                </v-time-picker>
            </v-menu>
        </v-col>

    </v-row>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

    data() {
        return {
            menuDate: false,
            menuTime: false,
            formattedDate: this.date,
        }
    },

    computed: {
        ...mapGetters('urgentCare', {
            mother: 'getMother',
        }),

        date() {
            return this.mother.deliveryDate
        },
    },

    watch: {
      date (val) {
        this.formattedDate = this.formatDate(val)
      },
    },

    methods: {

      formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}.${month}.${year}`
      },

      parseDate (date) {
        if (!date) return null

        const [day, month, year] = date.split('.')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },

      test() {
        console.log(this.mother)
      },

    },
}

</script>
